import React from "react";
import styles from "./Footer.module.css";
import StaysafeImg from "./united-nations-covid-19-response-KSUv52EZgGg-unsplash-min.jpg";

const Footer = () => {
  const onMouseOut = (e) => {
    e.target.style.color = "#cccccc";
  };

  

  return (
    <React.Fragment>
      <div className={styles.container}>
        <h6
          style={{
            display: "block",
            marginBottom: "0px",
            fontSize: 30,
            marginTop: "40px",
            textAlign: "center",
          }}
        >
          Created with care by{" "}
          <a
            href="https://in.linkedin.com/in/pavan-vanam-b7a3aba8"
            target="_blank"
            rel="noopener noreferrer"
            style={{ color: "rgba(0,0,255,0.6)", textDecoration: "none" }}
          >
            Pavan Vanam
          </a>
        </h6>
      </div>
      
      <div className={styles.container}>
        <h6
          style={{
            display: "block",
            marginBottom: "0px",
            fontSize: 16,
            marginTop: "20px",
            textAlign: "center",
          }}
        >
          Data sourced from{" "}
          <a
            href="https://github.com/mathdroid/covid-19-api"
            target="_blank"
            rel="noopener noreferrer"
            style={{ color: "rgba(0,0,255,0.6)", textDecoration: "none" }}
          >
            Mathdroid
          </a>
        </h6>
        
        
      </div>
      <div className={styles.container}>
      <p className={styles.aligncenter}><img  src={StaysafeImg} alt="Stay Safe in Covid 19 Pandemic" style={{}}/></p>

      </div>
    </React.Fragment>
  );
};

export default Footer;
